import React from 'react';
import ReactDOM from 'react-dom';

import katex from 'katex';

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import abi from './abi';

const Web3 = require("web3");

const address = "0xe559835d7979dae994331defaec2fba057e56c27";

const getWeb3 = () => {
  return new Web3(Web3.givenProvider);
}

const getContractInstance = () => {
  window.ethereum.enable();
  const web3 = getWeb3();
  return new web3.eth.Contract(abi, address);
}

class Index extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      id: "",
      locked: true
    }
    this.handleChange = this.handleChange.bind(this);
  }

  async componentDidMount() {
    const contract = getContractInstance();
    const result = await contract.methods.locked().call();
    this.setState({
      locked: result
    })
  }

  handleChange(e) {
    this.setState({
      id: e.target.value
    })
  }

  render() {

    return(
      <div>
        <h1>Equation Hunt</h1>
        <Link to="/rules">Rules</Link>
        <h2>
          Contract is currently: {
            this.state.locked ? "LOCKED" : "UNLOCKED"
          }
        </h2>
        <h2>Lookup</h2>
        <p>Enter the ID found at the cache to lookup the associated equation.</p>
        <label htmlFor="id">ID:</label>
        <input type="text" value={this.state.id} onChange={this.handleChange} />
        <button><Link to={`/solve/${this.state.id}`}>Lookup</Link></button>
      </div>
    )
  }
}

class Solve extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      repr : "loading...",
      answer: "",
      solvers: []
    }

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  async componentDidMount() {
    const contract = getContractInstance();
    const equation = await contract.methods.getEquation(this.props.match.params.id).call();
    const solvers = await contract.methods.getSolvers(this.props.match.params.id).call();
    this.setState({
      repr: equation,
      solvers: solvers
    });

    if(this.state.repr === "" ) {
      this.setState({
        repr : `âˆ…`
      })
    }
  }

  handleChange(e) {
    this.setState({answer: e.target.value})
  }

  async handleSubmit(e) {
    // 3 possible scenarios
    // Solves correctly for first timeout
    // Solve correctly but has already hasSolved REVERT
    // Solves incorrectly REVERT
    const web3 = getWeb3();
    const addresses = await web3.eth.getAccounts();
    const contract = getContractInstance();
    contract.methods.solve(this.props.match.params.id, Number(this.state.answer))
      .send({from: addresses[0]});
    this.setState({answer: ""});
  }


  render() {

    const createMarkup = () => {
      return {
        __html : katex.renderToString(this.state.repr, {output: "html"})
      }
    }

    return(
      <div>
        <h1>Solve for {this.props.match.params.id}</h1>
        <div dangerouslySetInnerHTML={createMarkup()} />
        <label htmlFor="answer">Answer:</label>
        <input type="number" name="answer" value={this.state.answer} onChange={this.handleChange}/>
        <button onClick={this.handleSubmit}>Check</button>
        <h2>Previous Solvers</h2>
        <ul>
          {
            this.state.solvers.map((solver) => {
              return(
                <li key={solver}>{solver}</li>
              )
            })
          }
        </ul>
        <Link to="/">Back to lookup</Link>
      </div>
    )
  }
}

const Rules = () => {
  return(
    <div>
      <h1>Rules</h1>
      <p>
        Please observe the following out of courtesy.
      </p>
        <ol>
          <li>Cache ID must be found at the site.</li>
          <li>Solve for reward only one time.</li>
          <li>Please do not give out the cache IDs or answers.</li>
        </ol>
      <Link to="/">Back to lookup</Link>
    </div>
  )
}

const App = () => {

  return(
    <Router basename="/eq-hunt">
      <Switch>
        <Route exact path="/">
          <Index />
        </Route>
        <Route path="/solve/:id" component={Solve} />
        <Route path="/rules">
          <Rules />
        </Route>
      </Switch>
    </Router>
  )
}

ReactDOM.render(<App />, document.getElementById('root'));
